.hover {
  &-border {
    &:hover {
      border-color: $light !important; } }
  &-shadow {
    &:hover {
      box-shadow: 0 0 10px rgba($primary-light, .5); } }
  &-scale-1 {
    &:hover {
      transform: scale(1.02); } } }


.arrow {
  position: relative;
  &::after {
    content: '';
    position: absolute;
    bottom: -52px;
    left: 50%;
    transform: translateX(-50%);
    border: solid 10px;
    border-top-color: $primary;
    border-right-color:  transparent;
    border-bottom-color:  transparent;
    border-left-color:  transparent; } }


.scrollbar-w {
  &::-webkit-scrollbar {
    height: 8px; }

  &::-webkit-scrollbar-thumb {
    border-radius: 3px;
    background-color: rgba($primary, .8); }

  &::-webkit-scrollbar-track {
    border-radius: 3px;
    box-shadow: inset 0 0 10px $light; } }
